export const textRequired = value => {
    if (value) return undefined;
    return ",   ";
}
export const emailRequired = value => {
    if (value && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)) return "  ";
    else if (!value) return ",   "
}
export const passwordRequired = value => {
    if (/^(?=.*[a-zA-Z]).{1,}$/.exec(value) === null) return "       ";
    return undefined;
}
export const LengthCreator = (minLen, maxLen) => (value) => {
    if (value.length > maxLen) return `  ${maxLen} `
    else if (value.length < minLen) return `  ${minLen} `
    return undefined;   
}